.point {
  width: 350px;
  position: absolute;
  right: 5%;
  z-index: 3;
  bottom: 30px;
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.javascript {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 40px;
  transform: rotate(-4deg);
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',  monospace;
  padding: 30px 40px 30px 120px;
  position: absolute;
  right: 7%;
  z-index: 4;
  bottom: 440px;
  color: #0a0038;
  font-size: 23px;
  text-shadow: 1px 1px 2px #ff6a00, 0 0 1em #5e00ff, 0 0 0.2em #fffb00;

  font-weight: bold;
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.reactjs {
  transform: rotate(4deg);
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',  monospace;
  padding: 5px 20px 5px 20px;
  position: absolute;
  right: 14%;
  z-index: 4;
  bottom: 315px;
  color: #0a0038;
  font-size: 23px;
