.moon {
  position: absolute;
  width: 250px;
  left: 28%;
  @media screen and (max-width: 991px) {
    left: auto;
    right: 5%;
  }
  @media screen and (max-width: 575px) {
    width: 150px;
  }
  z-index: 1;
  top: 25px;
  opacity: 0.5;
}

.fired {
  width: 550px;
  position: absolute;
  opacity: 0.3;
  left: 0;
  z-index: 2;
  bottom: 50px;
  @media screen and (max-width: 575px) {
    width: 250px;
  }
}