.root {
  min-height: 30px;
  border-top: 1px solid #efe5e4;
  background: rgba(229, 224, 218, 0.5);
  font-size: 14px;
  height: 280px;
  align-content: flex-end;

  padding-left: 15px;
  padding-right: 15px;

  padding-top: 30px;
  padding-bottom: 30px;

  margin-top: 50px;
}

.footer__links {
  margin: 30px auto 20px auto;

  display: grid;
  padding: 15px;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;

  max-width: 1400px;

  @media screen and (max-width: 1699px){
    max-width: 85%;
  }
  @media screen and (max-width: 1399px){
    max-width: 90%;
  }
  @media screen and (max-width: 1199px){
    max-width: 90%;
  }
  @media screen and (max-width: 991px){
    max-width: 95%;
  }

  h4 {
    margin-bottom: 25px;
    color: rgba(75, 57, 55, 0.88);
  }

  ul {
    padding: 0;
    list-style: none;

    li {
      margin-bottom: 10px;
    }
  }

  a {
    text-decoration: none;
    //color: #0a0038;
    color: rgba(75, 57, 55, 0.88);
    &:hover {
      text-decoration: underline;
    }
  }
}