.info {
  padding-left: 30px;
  padding-right: 30px;
  @media screen and (max-width: 767px) {
    padding-left: 15px;
    padding-right: 15px;
  }
  @media screen and (max-width: 575px) {
    padding-left: 0;
    padding-right: 0;
  }
  h2 {
    margin-top: 0;
    color: #8a46a9;
    text-align: center;
    margin-bottom: 40px;
  }

  ul {
    list-style: none;
    li {
      margin-bottom: 10px;
      &:before {
        margin-right: 10px;
        content: '🔗';
      }
    }
  }
}

.education__blocks {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 90px;
  @media screen and (max-width: 1399px) {
    grid-gap: 60px;
  }
  @media screen and (max-width: 1199px) {
    grid-gap: 40px;
  }
  @media screen and (max-width: 1199px) {
    grid-template-columns: 1fr 1fr;
  }
  @media screen and (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.education__block {
  background: rgba(245, 236, 255, 0.25);
  padding: 30px;

  border-radius: 10px;
  border: 1px solid #cccccc;

  h3 {
    margin-top: 0;
    margin-bottom: 30px;
  }
}