.info {
  display: grid;
  grid-template-columns: 2fr 5fr;
  @media screen and (max-width: 991px) {
    grid-template-columns: 1fr;
  }
  grid-column-gap: 0px;
  align-items: center;

  //padding-top: 30px;
  padding-bottom: 30px;

  img {
    position: relative;
    bottom: -80px;
    width: 350px;
    border-radius: 30px;
    filter: grayscale(0.30);
    @media screen and (max-width: 1199px) {
      width: 300px;
    }
    &:hover {
      filter: none;
      transition: filter 2s;
    }
  }
  h2 {
    margin-top: 0;
    margin-bottom: 35px;
    color: #8a46a9;
  }
  ul {
    padding: 0;
    li {
      margin-bottom: 10px;
    }
    margin-top: 0;
    margin-bottom: 0;
    hr {
      opacity: 0.3;
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
}

.personalSkills {
  color: rgba(62, 67, 72, 0.8);
  margin: 0 0 0 auto;
  font-size: 16px;
  padding-bottom: 40px;
}

.row {
  display: grid;
  grid-template-columns: 1fr 3fr;
  @media screen and (max-width: 767px) {
    grid-template-columns: 1fr;
    margin-bottom: 10px;
  }
  border: 1px solid rgba(204, 204, 204, 0.5);
  margin-bottom: 2px;
}

.col {
  padding: 15px 30px;
  @media screen and (max-width: 1199px) {
    font-size: 14px;
  }
  &:nth-child(odd) {
    background: rgba(52, 124, 177, 0.05);
    color: rgba(62, 67, 72, 0.8);
    min-width: 150px;
    @media screen and (max-width: 1199px) {
      padding: 10px 15px;
      align-items: center;
      display: flex;
    }
  }
}

.cat {
  @media screen and (max-width: 991px) {
    display: none;
  }
}