.header {
  height: 60px;
  background: #0a0038;
  box-shadow: rgba(74, 66, 80, 0.2) 0px 5px 5px 0px;

  position: fixed;
  z-index: 999;
  width: 100%;

  padding-left: 30px;
  padding-right: 30px;

  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 767px) {
    max-width: 100%;
    overflow-x: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
  @media screen and (max-width: 575px) {
    max-width: 100%;
    overflow-x: auto;
    padding-left: 5px;
    padding-right: 5px;
  }

  a {
    color: #f8eaef;
    &:hover { }
    font-family: 'Raleway', sans-serif;
    text-decoration: none;
    margin: 15px;
    @media screen and (max-width: 991px){
      font-size: 14px;
    }
    @media screen and (max-width: 767px){
      font-size: 12px;
    }
    @media screen and (max-width: 575px){
      font-size: 10px;
      margin: 10px;
    }
  }
  a.home-link {
    &:hover {
      color: #f8eaef!important;
    }
  }
}

.links {
  @media screen and (max-width: 1199px){
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
}

.laptop {
  @media screen and (min-width: 768px) {
    display: flex;
  }
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.mobile {
  @media screen and (min-width: 768px) {
    display: none;
  }
  @media screen and (max-width: 767px) {
    display: flex;
  }
  @media screen and (max-width: 575px) {
    &_open {
      display: flex;
      flex-direction: column;
      position: absolute;
    }
  }
}

.title {
  position: absolute;
  left: 30px;
  @media screen and (max-width: 575px) {
    span {
      display: none;
    }
  }
  a {
    color: #f8eaef;
    font-size: 22px;
    font-weight: 600;
    margin: 0;
  }
}
