.welcome {
  position: absolute;
  width: 700px;
  height: 185px;
  top: 1%;
  right: 0%;

  @media screen and (max-width: 1399px) {
    width: 600px;
    right: 2%;
  }
  @media screen and (max-width: 1199px) {
    width: 500px;
    right: 5%;
  }
  @media screen and (max-width: 991px) {
    width: 800px;
    top: 25%;
    right: 0%;
  }
  @media screen and (max-width: 767px) {
    width: auto;
  }
  @media screen and (max-width: 575px) {
    top: 22%;
    right: auto;
  }

  border-radius: 50%;
  padding: 20px 85px;
}

.welcome__text1 {
  font-family: 'Comforter Brush', cursive;
  margin-top: 20px;
  color: #0a0038;
  font-style: italic;
  font-size: 54px;
  font-weight: normal;

  @media screen and (max-width: 1399px){
    font-size: 50px;
  }
  @media screen and (max-width: 1199px){
    font-size: 40px;
  }
  @media screen and (max-width: 991px) {
    font-size: 50px;
  }
  @media screen and (max-width: 575px) {
    color: white;
    font-size: 25px;
  }
}

.welcome__text2 {
  font-family: 'Comforter Brush', cursive;
  color: black;
  font-style: italic;
  font-size: 54px;
  font-weight: normal;

  @media screen and (max-width: 1399px){
    font-size: 50px;
  }
  @media screen and (max-width: 1199px){
    font-size: 40px;
  }
  @media screen and (max-width: 991px) {
    font-size: 50px;
  }
  @media screen and (max-width: 575px) {
    color: white;
    font-size: 25px;
    display: none;
  }
}