.mobileHeader {

  position: fixed;
  z-index: 999;
}

.header {
  min-height: 60px;
  width: 100vw;
  justify-content: space-between;

  background: #0a0038;
  box-shadow: rgba(74, 66, 80, 0.2) 0px 5px 5px 0px;


  display: flex;
  align-items: center;
  @media screen and (max-width: 767px) {
    max-width: 100%;
    overflow-x: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
  @media screen and (max-width: 575px) {
    max-width: 100%;
    overflow-x: auto;
    padding-left: 5px;
    padding-right: 5px;
  }

  a {
    color: #f8eaef;
    &:hover { }
    font-family: 'Raleway', sans-serif;
    text-decoration: none;
    margin: 15px;
    @media screen and (max-width: 991px){
      font-size: 14px;
    }
    @media screen and (max-width: 767px){
      font-size: 12px;
    }
    @media screen and (max-width: 575px){
      font-size: 10px;
      margin: 10px;
    }
  }
  a.home-link {
    &:hover {
      color: #f8eaef!important;
    }
  }
}

.links {
    margin-top: 60px;
    padding: 45px 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    //background: #020024;
    background: #130f57;
    //background: #020024;
    position: absolute;
    height: 100vh;
    top: 0;
    z-index: 999;

  a {
    display: block;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    color: white;
    text-decoration: none;
  }
}

.laptop {
  @media screen and (min-width: 768px) {
    display: flex;
  }
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.mobile {
  @media screen and (min-width: 768px) {
    display: none;
  }
  @media screen and (max-width: 767px) {
    display: flex;
  }
}

.title {
  margin-left: 15px;
  position: relative;
  a {
    color: #f8eaef;
    font-size: 18px;
    font-weight: 600;
    span {
      display: inline-block;
      position: relative;
      top: -2px;
      left: 2px;
    }
  }
}

.button {
  margin-right: 15px;
  font-size: 25px;
  color: white;
}