.info {
  padding-top: 30px;
  padding-bottom: 60px;
  border-bottom: 1px solid rgba(30, 144, 255, 0.12);

  h2 {
    margin-top: 0;
    margin-bottom: 25px;
    color: #8a46a9;
  }

  ul {
    list-style: none;
    padding: 0;
    li {
      margin-bottom: 10px;
      &:before {
        margin-right: 10px;
        content: '🔗';
      }
    }
    @media screen and (max-width: 767px) {
      &:first-child {
         margin-bottom: 60px;
      }
    }
    margin-top: 0;
    margin-bottom: 0;
  }
}

.listWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  @media screen and (max-width: 767px) {
    grid-template-columns: 1fr;
  }
}

.personalSkills {
  color: rgba(62, 67, 72, 0.8);
  margin: 0 0 0 auto;
  text-align: justify;
  font-size: 16px;
  padding-top: 40px;
}