.info {
  padding-bottom: 60px;
  border-bottom: 1px solid rgba(30, 144, 255, 0.12);
  h2 {
    font-size: 24px;
    text-align: center;
    margin: 0;
    color: #347cb1;
    text-transform: uppercase;
    font-weight: 600;
  }
  button {
    margin-top: 12px;
    color: rgba(62, 67, 72, 0.99);
  }
  ul {
    list-style: none;
    li {
      margin-bottom: 10px;
      &:before {
        margin-right: 10px;
        content: '🔗';
      }
    }
  }
}

