.root {
  position: relative;
  margin: 0 auto;
  border: 1px solid rgb(2,0,36);
  max-width: 1300px;
  height: calc(100vh - 110px);
  background: rgb(2,0,36);
  background: linear-gradient(0deg, rgba(2,0,36,1) 20%, rgba(9,9,121,1) 55%, rgba(0,212,255,1) 100%);
  border-radius: 5px;
}

.flower {
  width: 420px;
  position: absolute;
  left: 20px;
  z-index: 2;
  top: 5px;
  @media screen and (max-width: 575px) {
    width: 260px;
  }
}