.root {
  background: #fcfbfa;
  max-width: 1400px;
  margin: 0 auto;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  position: relative;
}

.info {
  position: relative;
  box-shadow: 10px 0 5px -2px rgba(119, 105, 105, 0.2);
  background: rgb(229, 243, 253);

  border-top: 1px solid rgba(30, 144, 255, 0.25);
  border-left: 4px solid rgba(30, 144, 255, 0.1);
  border-bottom: 1px solid rgba(30, 144, 255, 0.12);

  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  grid-gap: 80px;
  @media screen and (max-width: 767px) {
    grid-gap: 0;
  }

  padding: 60px 40px 60px 40px;
  font-size: 18px;
  border-top-right-radius: 45px;
  border-bottom-right-radius: 45px;

  @media screen and (max-width: 1399px) {
    grid-gap: 60px;
    padding: 50px 30px 50px 30px;
  }

  @media screen and (max-width: 1199px){
    grid-template-columns: 1fr;
    grid-gap: 40px;
    padding: 40px 20px 40px 20px;

    & > div:first-child {
      order: 1;
    }
    & > div:last-child {
      display: flex;
      justify-content: center;
    }
  }
}

.text {
  position: relative;
  color: rgba(62, 67, 72, 0.99);
  padding: 20px 40px;
  background-image: url("../../../../assets/images/main/bg-white.png");
  border-radius: 2px;
  border: 1.5px solid rgba(179, 144, 109, 0.3);
  h3 {
    font-style: italic;
    font-weight: 600;
    text-align: center;
    margin: 0 0 30px 0;
  }
}

.photo {
  width: 350px;
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.1) 2.4px 2.4px 3.2px;
  transform: rotate(5deg);
  @media screen and (max-width: 1399px) {
    width: 300px;
  }
  @media screen and (max-width: 1199px) {
    width: 320px;
    transform: rotate(0deg);
  }
  @media screen and (max-width: 767px) {
    max-width: 100%;
  }
}

.pin {
  transform: rotate(-10deg);
  margin-left: 15px;
  width: 50px;
  position: absolute;
  top: 0;
  left: -30px;
}